"use client"

import React, { useEffect, useRef, useState } from 'react';
import Link from 'next/link';
import { Cinzel } from 'next/font/google';
import gsap from 'gsap';
import { ClipperLogoSvg, UserSvg } from '@/lib/utils/helper';

const cinzelFont = Cinzel({ subsets: ['latin'] });

const Navbar = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isAnimationReady, setIsAnimationReady] = useState(false);
  const menuRef = useRef(null);
  const menuItemsRef = useRef(null);
  const menuBackgroundRef = useRef(null);
  const timelineRef = useRef(null);

  const menuItems = [
    { title: 'Home', href: '/' },
    { title: 'Buy Credits', href: '/account/credits' },
    { title: 'Team', href: '/team' },
    { title: 'Contact us', href: '/contactus' },
    { title: 'SignIn', href: '/signin' }
  ];

  useEffect(() => {
    // Initial setup - hide everything
    const menuBg = menuBackgroundRef.current;
    const menuItemsEl = menuItemsRef.current;

    gsap.set(menuBg, {
      clipPath: 'circle(0% at top right)'
    });

    gsap.set(menuItemsEl.children, {
      y: 50,
      opacity: 0
    });

    // Create the timeline
    timelineRef.current = gsap.timeline({
      paused: true,
      defaults: { ease: 'power3.inOut' }
    });

    // Build the timeline
    timelineRef.current
      .to(menuBg, {
        clipPath: 'circle(150% at top right)',
        duration: 1
      })
      .to(menuItemsEl.children, {
        y: 0,
        opacity: 1,
        duration: 0.8,
        stagger: 0.1,
        ease: 'power3.out',
      }, '-=0.5');

    // Mark animations as ready
    setIsAnimationReady(true);

    return () => {
      if (timelineRef.current) {
        timelineRef.current.kill();
      }
    };
  }, []); // Only run once on mount

  useEffect(() => {
    if (!timelineRef.current || !isAnimationReady) return;

    if (isMenuOpen) {
      timelineRef.current.play();
    } else {
      timelineRef.current.reverse();
    }
  }, [isMenuOpen, isAnimationReady]);

  return (
    <>
      <nav className="fixed top-0 right-0 z-50 flex w-full items-center justify-between px-10 max-md:px-5 h-[12vh]">
        <Link href="/">
          <div className={`${cinzelFont.className} flex items-center`}>
            <h3 className="text-2xl max-md:text-xl">FILM</h3>
            <div className="h-[60px] w-[60px] max-md:h-[45px] max-md:w-[45px]">
              <ClipperLogoSvg />
            </div>
            <h3 className="text-2xl max-md:text-xl">STAKE</h3>
          </div>
        </Link>
        <div className="flex items-center gap-3">
          <Link href="/signin">
            <button className="cursor-pointer rounded-full border border-white/5 bg-[#221E1C]/40 backdrop-blur-md px-5 py-2 max-md:px-2 transition-all duration-300 ease-in-out hover:bg-[#221E1C]/60 hover:border-white/20 hover:scale-105 hover:shadow-lg hover:shadow-black/20">
              <div className="flex items-center gap-4">
                <p className="max-md:hidden">Signin</p>
                <UserSvg />
              </div>
            </button>
          </Link>
          <button 
            onClick={() => setIsMenuOpen(!isMenuOpen)}
            className="cursor-pointer rounded-full border border-white/5 bg-[#221E1C]/40 backdrop-blur-md px-2 py-2 transition-all duration-300 ease-in-out hover:bg-[#221E1C]/60 hover:border-white/20 hover:scale-105 hover:shadow-lg hover:shadow-black/20 z-50"
          >
            <div className={`w-4 h-3.5 flex flex-col justify-between transition-all duration-300 ${isMenuOpen ? 'rotate-45' : ''}`}>
              <span className={`w-full h-[1px] bg-white transform transition-all duration-300 ${isMenuOpen ? 'translate-y-[6px] rotate-90' : ''}`}></span>
              <span className={`w-full h-[1px] bg-white transition-all duration-300 ${isMenuOpen ? 'opacity-0' : ''}`}></span>
              <span className={`w-full h-[1px] bg-white transform transition-all duration-300 ${isMenuOpen ? '-translate-y-[6px]' : ''}`}></span>
            </div>
          </button>
        </div>
      </nav>

      {/* Menu Overlay */}
      <div ref={menuRef} className={`fixed inset-0 z-40 pointer-events-none ${!isAnimationReady ? 'invisible' : ''}`}>
        {/* Menu Background */}
        <div 
          ref={menuBackgroundRef}
          className="absolute inset-0 bg-[#121212]/95 backdrop-blur-lg"
        ></div>

        {/* Menu Content */}
        <div className="relative h-full w-full flex items-center justify-center">
          <div 
            ref={menuItemsRef}
            className={`${cinzelFont.className} flex flex-col items-center justify-center space-y-8 ${isMenuOpen ? 'pointer-events-auto' : 'pointer-events-none'} invisible`}
            style={{ visibility: isAnimationReady ? 'visible' : 'hidden' }}
          >
            {menuItems.map((item, index) => (
              <Link
                key={index}
                href={item.href}
                className="relative group"
                onClick={() => setIsMenuOpen(false)}
              >
                <div className="overflow-hidden">
                  <span className="text-5xl font-bold text-white/80 hover:text-white transition-colors duration-300 block">
                    {item.title}
                  </span>
                </div>
                <span className="absolute -bottom-2 left-0 w-0 h-[1px] bg-white transition-all duration-300 group-hover:w-full"></span>
              </Link>
            ))}
          </div>
        </div>
      </div>
    </>
  );
};

export default Navbar;